/*一本书左右形式*/
.seciton-bd .editor-b-dl {
	position: relative;
	padding:15px 15px 5px;
	overflow:hidden;
}
.seciton-bd .editor-b-dl .nowRead{
	position: absolute;
	top:32px;right:0;padding:0 5px;height:24px;line-height:24px;background:#10c1ae;
	border-radius: 4px 0 0 4px;
}
.seciton-bd .editor-b-dl .nowRead a{
	display:block;color:#fff;font-size:12px;
	span{
		margin-left:8px;
		display:inline-block;
		width:6px;
		height:10px;
		background:url(../../img/wx/laikan/icon/lkIndex-other.png?v=6) no-repeat;
    background-position:-32px -40px;
    -webkit-background-size: 50px auto;
    background-size: 50px auto;
	}
}
.seciton-bd .editor-b-dl a {
	width: 100%;
	display: -moz-box;
	display: -webkit-box;
	display: box
}

.editor-b-dl dt {
	margin-right: 10px;
	width:80px;height:112px;
	position: relative
}

.editor-b-dl dt span {
	display: block;
	position: absolute;
	right: 10px;
	bottom:0;
	border-radius:15px 0 0 0;width:48px;padding-left:2px;height:18px;line-height:18px;font-size:10px;color:#fff;text-align:center;
}

.editor-b-dl img {
	width: 80px;box-shadow:1px 1px 1px #cacaca
}

.editor-b-dl dd {
	-moz-box-flex: 1.0;
	-webkit-box-flex: 1.0;
	box-flex: 1.0;
	position: relative;
	overflow: hidden
}

.editor-b-dl dd span {
	display: block
}

.editor-b-dl dd span.book-name {
	font-size: 16px;
	color: #333;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis
}

.editor-b-dl dd span.author-name {
	font-size: 12px;
  line-height:12px;
	padding:6px 0;
	position: relative
}

.editor-b-dl dd span.book-brief {
	color: #333;
	line-height: 18px;
	font-size: 12px;
	max-height:38px;margin-bottom:4px;
	overflow: hidden;
	word-break: break-all!important;
	word-wrap: break-word!important
}
.editor-b-dl dd del{display:block;color:#999;font-size:12px;}
.editor-b-dl dd p{color: #999;position:relative;width:100%;}
.editor-b-dl dd p span{float:left;display:block;color:#fff;font-size:12px;}
.editor-b-dl dd p span.left{background:#f88265;border-radius:3px;padding:0px 5px;margin-right:6px;line-height:20px;}
.editor-b-dl dd p i{font-style:normal;display:inline-block;margin-right:6px;border-radius:3px;
padding:0 5px;line-height:20px;background:#fd6974}/*通用*/

.editor-b-dl dd p i:nth-child(1){background:#72bfeb}
.editor-b-dl dd p i:nth-child(2){background:#83d598}


#editor-b-dl:last-child {
	padding-bottom: 15px
}
