.otherMod{
	margin:-10px 0 10px;
  display:-webkit-box;
  display:-moz-box;
  display:box;
  height:50px;
  line-height:50px;
  background:#fcfcfc;
  border-top:solid 1px #eee;
  overflow:hidden;
  li{
    -webkit-box-flex:1;
    -moz-box-flex:1;
    box-flex:1;
    border-right:solid 1px #eee;
    &:last-child{
      border-right:none;
    }
    a{
      display:block;
      width:100px;
      margin:0 auto;
      color:#333;
      font-size:1.4rem;
      overflow: hidden;
      span{
        display:block;
        &.icon{
          margin:13px 10px 0 0;
          width:24px;
          height:24px;
          float:left;
          background: url(../../img/wx/laikan/icon/icon-index-nav.png?v=201704051112) no-repeat;
          background-position: 0 -450px;
          -webkit-background-size: 100px auto;
          background-size: 100px auto;
          &.m{
            background-position: -50px 0;
          }
          &.r{
            background-position: -50px -44px;
          }
        }
        &.text{
          float:left;
        }
      }
    }
  }
}
